.gridPage {
  padding: 20px;
}

.gridContainer {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 10px;
}

.gridItem {
  padding: 10px;
  text-align: left;
}

.imagePlaceholder {
  width: 100%;
  background-color: #ddd;
  margin-bottom: 10px;
  position: relative; /* 상대 위치 설정 */
  padding-bottom: 100%; /* 높이를 너비의 100%로 설정 (정사각형) */
}


.headtext {
  font-weight: bold;
}

.itemDetails p {
  font-size: 12px;
}

@media (max-width: 767px) {
  .gridPage {
    padding: 0px;
  }
  
  .gridContainer {
    grid-template-columns: 1fr; /* 한 줄 배치 */
    padding: 0px;
  }
  
  .gridItem {
    padding: 0px;
    text-align: left;
  }
  .itemDetails{
    margin: 0 2 0px;
  }
  .itemDetails p {
    font-size: 12px; /* 글자 크기 조정 */
  }

  .imagePlaceholder {
    width: 90%;
    padding-bottom: 90%; /* 높이를 너비의 100%로 설정 (정사각형) */
    margin: auto;
  }

}
